import React from 'react';

const PersonalInfo = ({ profile }) => {
  const {
    email,
    firstName,
    middleName,
    lastName,
    isActive,
    companyName,
    uniqueUserId,
    userName,
    organization
  } = profile;

  return (
    <>
      {uniqueUserId && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Unique User Id</label>
          <label className="col-sm-10 col-form-label">{uniqueUserId}</label>
        </div>
      )}
      {userName && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">User Name</label>
          <label className="col-sm-10 col-form-label">{userName}</label>
        </div>
      )}
      {firstName && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">First Name</label>
          <label className="col-sm-10 col-form-label">{firstName}</label>
        </div>
      )}
      {middleName && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Middle Name</label>
          <label className="col-sm-10 col-form-label">{middleName}</label>
        </div>
      )}
      {lastName && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Last Name</label>
          <label className="col-sm-10 col-form-label">{lastName}</label>
        </div>
      )}
      {email && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Email</label>
          <label className="col-sm-10 col-form-label">{email}</label>
        </div>
      )}
      {companyName && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Company Name</label>
          <label className="col-sm-10 col-form-label">{companyName}</label>
        </div>
      )}
      {organization && (
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Organization</label>
          <label className="col-sm-10 col-form-label">{organization}</label>
        </div>
      )}
      <div className="form-group row">
        <label className="col-sm-2 col-form-label">Status</label>
        <label className="col-sm-10 col-form-label">{isActive ? 'Active' : 'Inactive'}</label>
      </div>
    </>
  );
};

export { PersonalInfo };
